main#page-content[name=Posts] {

    #posts {
        list-style: none;
        margin: 0;
        padding: 0;

        //h3.year {
        //    margin-top: 4rem;
        //}

        .post {
            margin-bottom: 1rem;

            span {
                color: #ccc;
                display: block;
                font-size: 14px;
                font-weight: 700;
                margin: 0;
                text-transform: uppercase;
            }

            a {
                border-bottom: 3px solid #7ef1f9;
                color: #0151ba;
                cursor: pointer;
                font-size: 20px;
                position: relative;
                text-decoration: none;

                &:hover {
                    &::before {
                        visibility: visible;
                        transform: scaleX(1);
                    }
                }

                &::before {
                    background-color: #0151ba;
                    bottom: -3px;
                    content: "";
                    height: 3px;
                    left: 0;
                    position: absolute;
                    transform: scaleX(0);
                    transition: all 0.10s ease-in-out 0s;
                    visibility: hidden;
                    width: 100%;
                }


            }
        }
    }

}


